.logincontainerTeacher{
    background: white;
    border-radius: 35px;
    width:30%;
    margin:5rem auto;
    padding:5px;
    box-shadow:0px 0px 10px gray;
}

.eachel{
margin:1rem;
width:90%
}

.banner{
    padding:1rem;
    font-weight: 600;
    font-size: 20px;
}

.bannerhead{
    background-color:#3E51B5;
    border-radius: 35px;
    color:white;
    font-weight: 600;
    padding:10px;
    box-shadow: 0px 0px 5px #3E51B5;
    font-size: 30px;
}

@media screen and (max-width:895px){
    .logincontainerTeacher{
        width:60%;
    }
}


@media screen and (max-width:442px){
    .logincontainerTeacher{
        width:90%;
    }
}