.main{
  width: 65%;
    box-shadow: 0px 0px 5px 3px lightgrey;
    text-align: center;
    margin: auto;
    padding-top: 10px;
    background-color: white;
}

.txtarea{
    width: 65%
}

.imageDiv{
  
    width: 35%;
    margin: 50px  auto;
    height: 270px;
}

.imageDiv img{
    width: 100%;
    height: 90%;
    border-radius: 5%;
    
} 

@media screen and (max-width: 1030px) {
    .imageDiv{
      height: 240px;
    }
}
@media screen and (max-width: 840px) {
    .imageDiv{
      height: 200px;
    }
}
@media screen and (max-width: 720px) {
    .imageDiv{
      height: 170px;
    }
}
@media screen and (max-width: 600px) {
    .imageDiv{
      height: 130px;
    }
}
@media screen and (max-width: 450px) {
    .imageDiv{
      height: 100px;
    }
    .main{
      width:90%;
    }
}